body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  margin: auto; }
  .App .container {
    box-shadow: 2px 2px 1px gainsboro;
    border: solid thin gainsboro;
    -webkit-flex-basis: 32%;
            flex-basis: 32%;
    height: 500px;
    margin: 3px; }

.search {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }
  .search .search-results {
    -webkit-flex-basis: 86%;
            flex-basis: 86%;
    overflow-y: scroll; }
  .search .next-button {
    float: left;
    margin: 2px; }
  .search .view-buttons {
    float: right;
    margin: 2px; }

.track {
  text-align: left;
  margin: 5px;
  border-bottom: solid thin gainsboro;
  padding: 4px;
  box-shadow: 1px 1px 1px gainsboro;
  font-size: 15px;
  cursor: pointer; }
  .track:hover {
    background-color: beige; }
  .track:active {
    box-shadow: inset 0 0 10px gainsboro; }

.art {
  position: relative;
  text-align: center; }
  .art .track-data {
    position: absolute;
    width: 200px;
    margin: auto auto 5px auto;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    left: 0;
    right: 0;
    color: wheat; }
  .art:hover {
    background-color: beige; }
  .art:active {
    box-shadow: inset 0 0 10px gainsboro; }

.btn {
  font-size: 17px; }
  .btn .next-button {
    float: left;
    margin: 2px; }
  .btn .view-buttons {
    float: right;
    margin: 2px; }

.input-wrapper {
  margin: 2px;
  display: -webkit-flex;
  display: flex;
  width: 100%; }
  .input-wrapper .input {
    width: 100%; }

